import React, { useState } from 'react';
import NumericalFilter from '@/pages/EditPage/FactListView/NumericalFilter';
import { Divider, Button, Tooltip, Tag, Slider ,Spin} from "antd";
import config from '@/axios/config';
import draw from '@/pages/EditPage/FactListView/BarBrush';
import ConvertForms from '@/constant/config';
import OperationType from '@/constant/OperationType'
import activitiesCode from '@/axios/activitiesCode';
import record_activity from '@/axios/recordactivities';
import * as api from '@/axios/api';
import Fact from '@/model/fact';
import { genFactSentence } from '@/sentencer/index';
import ConvertType from '@/constant/ConvertType';
import uuidv4 from 'uuid/v4';

const addFactNum = 10;
const { CheckableTag } = Tag;
const plur = require('plur');



const convertField = function (field, param = 'single') {
    let gb = field
    if (param === 'single')
        return gb
    else if (param === 'plural') {
        if (gb.indexOf(' of ') !== -1) {
            let gbWords = gb.split(" ")
            let gbWordIndex = gbWords.indexOf("of") - 1
            if (gbWordIndex > -1) {
                let plurWord = plur(gbWords[gbWordIndex], 2)
                return gb.replace(gbWords[gbWordIndex], plurWord)
            }
        } else
            return plur(gb, 2)
    }
}
export default class DataFilter extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
        }
    }
    
    getGenerateURL = (type) => {
        switch (type) {
            case ConvertForms.DATACOMICS:
                return config.url.generateGraphRandomly;
            case ConvertForms.FACTSHEET:
            default:
                return config.url.generateRandomly;
        }
    }
    
    addFact = () => {
        let typeForm = ConvertForms[0];
        this.setState({
            operateState: OperationType.ADDING
        })
        let type = typeForm.name;
        //TODO:fact
        this.adding(this.getGenerateURL(type)).then(response => {
            const facts = response.data.story.facts;
            switch (type) {
                case ConvertType.DATACOMICS:
                case ConvertType.FACTSHEET:
                case ConvertType.DASHBOARD:
                case ConvertType.DATAVIDEOS:
                default:
                    let tempFacts = [];
                    for (let factDict of facts) {
                        let fact = new Fact(
                            factDict['type'],
                            factDict['measure'],
                            factDict['subspace'],
                            factDict['groupby'],
                            factDict['focus'],
                            factDict['parameter'],
                            "", // chart
                            factDict['score'],
                            factDict['information'],
                            factDict['significance']
                        );
                        fact.uuid = uuidv4();
                        tempFacts.push(fact);
                    }
                    this.props.addFact(tempFacts);
                    console.log(tempFacts);
                    break;
            }
        });
    }

    adding = (url) => {
        return new Promise(async (resolve, reject) => {
            let fileName = this.props.fileName;
            let filters = _.cloneDeep(this.props.filtersLocal);

            let data;
            if (filters)
                data = {
                    file_name: fileName,
                    max_story_length: addFactNum,
                    filters: JSON.stringify(filters),
                    method: 'mcts'
                }

            const response = await api.generate(url, data)
            console.log('response-adding', response)
            if (response.data.fail) {
                this.setState({
                    operateState: OperationType.ADD_FAILED,
                    errorMessage: response.data.fail
                })
                reject()
                record_activity(activitiesCode.generateMoreFacts, 0, { filename: fileName, response: response });
            }
            if (response.data.error) {
                this.setState({
                    operateState: OperationType.ADD_FAILED,
                    errorMessage: response.data.error
                })
                reject()
                record_activity(activitiesCode.generateMoreFacts, 0, { filename: fileName, response: response });
            }
            this.setState({
                operateState: OperationType.GENERATED
            })
            resolve(response)
            record_activity(activitiesCode.generateMoreFacts, 1, { filename: fileName, response: response });
        })
    }

    //todo:factaddfact adding
    //Regeneratefact
    filteredgenerate = (url) => {
        return new Promise(async (resolve, reject) => {
            let fileName = this.props.fileName;
            let filters = _.cloneDeep(this.props.filters);

            this.props.updateFiltersChangeFlag(false)
            this.props.updateFiltersLocal(this.props.filters, this.props.filtersTitle, this.props.datares, this.props.schemaFilter)
            this.setState({
                operateState: OperationType.GENERATING
            });
            this.props.isUpdateEditing(true);

            let data = {
                file_name: fileName,
                filters: JSON.stringify(filters),
                max_story_length: 6,
                method: "mcts",
                // filters: JSON.stringify([this.state.filters[0]]),
            }

            this.props.unusedFacts.map((d, i) => {
                this.props.deleteUnusedFact(0)
            })

            const response = await api.generate(url, data)

            if (response.data.fail) {
                console.log('fail')
                this.setState({
                    operateState: OperationType.ADD_FAILED,
                    errorMessage: response.data.fail
                })
                reject()
                record_activity(activitiesCode.editTopic, 0, { filename: fileName, filters: filters });
            }
            if (response.data.error) {
                console.log('error')
                this.setState({
                    operateState: OperationType.ADD_FAILED,
                    errorMessage: response.data.error
                })
                reject()
                record_activity(activitiesCode.editTopic, 0, { filename: fileName, filters: filters });
            }
            const facts = response.data.story.facts;
            let tempFacts = [];

            for (let factDict of facts) {
                let fact = new Fact(
                    factDict['type'],
                    factDict['measure'],
                    factDict['subspace'],
                    factDict['groupby'],
                    factDict['focus'],
                    factDict['parameter'],
                    "", // chart
                    factDict['score'],
                    factDict['information'],
                    factDict['significance']
                )
                fact.generatedScript = genFactSentence(fact, this.state.lang)
                tempFacts.push(fact);
            }
            this.props.generateStory(tempFacts.slice(), [
                "none",
                "similarity",
                "similarity",
                "similarity",
                "similarity",
                "similarity"
            ], 1);

            //video
            setTimeout(() => {
                let nowFacts = _.cloneDeep(this.props.facts);
                let schemaFilter = _.cloneDeep(this.props.schemaFilterLocal);//
                let nowFileName = _.cloneDeep(this.props.fileName);
                let datares = _.cloneDeep(this.props.dataresLocal);//
                let schema = _.cloneDeep(this.props.schema);
                this.props.initialVideoSlice(nowFacts, schemaFilter, nowFileName, datares, schema);
                this.props.initTimeLineBarWidth(nowFacts.length);
                this.props.setPosition(0);
                //
                this.props.playVideoSlice(0);
            }, 1);
                
            
            this.setState({
                operateState: OperationType.GENERATED
            })

            if (!this.props.unusedFacts.length) this.addFact()

            this.props.isUpdateEditing(false)
            resolve(response)
            record_activity(activitiesCode.editTopic, 1, { filename: fileName, filters: filters });
        })
    }


    render() {
        const { intl, initDone, schema, } = this.props
        // CategoricalFilter
        let description_all = "All ";
        let description_select = "Selected ";
        let tip = initDone && intl.get("adding");

        const CategoricalFilter = (props) => {
            let values = props.info.values;
            let field = props.info.field;
            let filter = props.filters.filter(filter => filter.field === field);
            // console.log('CategoricalFilter - filter', filter)
            let selectedTags = filter[0].value;

            return (
                <div className="CategoricalFilter">
                    <div className="topicContent">
                        <Tooltip title={field}>
                            <p className="fieldText">{field}</p>
                        </Tooltip>
                    </div>
                    <div className="topicDetails">
                        {values.map(tag => (
                            <CheckableTag
                                key={tag}
                                checked={selectedTags.indexOf(tag) > -1}
                                onChange={(checked) => {

                                    let nextSelectedTags = checked ? [...selectedTags, tag] : selectedTags.filter(t => t !== tag);
                                    //  setSelectedTags(nextSelectedTags);

                                    let filter = {
                                        'field': props.info.field,
                                        'type': 'equal',
                                        'value': nextSelectedTags
                                    }
                                    let pluraltitle;
                                    pluraltitle = convertField(props.info.field, 'plural')


                                    let filterTitle = nextSelectedTags.length >= 3 ?
                                        (nextSelectedTags.length === values.length ?
                                            { 'field': props.info.field, 'type': props.info.type, 'title': description_all + pluraltitle } :
                                            { 'field': props.info.field, 'type': props.info.type, 'title': description_select + pluraltitle }
                                        ) :
                                        { 'field': props.info.field, 'type': props.info.type, 'title': nextSelectedTags.join(',') };


                                    let filters = props.filters;
                                    let filtersTitle = props.filtersTitle;


                                    let findFilter = false;
                                    let findFilterTitle = false;
                                    for (let i = 0; i < filters.length; i++) {
                                        if (filters[i].field === filter.field) {
                                            filters[i] = filter;
                                            findFilter = true;
                                            break;
                                        }
                                    }

                                    for (let i = 0; i < filtersTitle.length; i++) {
                                        if (filtersTitle[i].field === filterTitle.field) {
                                            filtersTitle[i] = filterTitle;
                                            findFilterTitle = true;
                                            break;
                                        }
                                    }

                                    if (findFilter === false) {
                                        filters.push(filter);
                                    }

                                    if (findFilterTitle === false) {
                                        filtersTitle.push(filterTitle);
                                    }

                                    props.updateFilters(filters);
                                    props.updateFiltersTitle(filtersTitle);
                                    props.updateFiltersChangeFlag(true);
                                    this.props.orderFacts(0, 0);
                                    //props.filteredgenerate(props.postUrl, filter);
                                }}
                            >
                                {/\r/.test(tag) === true ? 'Null' : tag}
                            </CheckableTag>
                        ))}
                    </div>
                    <Divider />
                </div>

            )

        };

        // TemporalFilter
        const TemporalFilter = (props) => {


            let values = props.info.values;
            let field = props.info.field;

            let filter = props.filters.filter(filter => filter.field === field);
            const [defaultValue, setDefaultValue] = useState(filter[0].value2);

            let marks = {};

            // let minIndex = values.indexOf(filter.value[0]);
            // let maxIndex = values.indexOf(filter.value[1]);
            for (let i = 0; i < values.length; i++) {
                if (i === 0) {
                    marks[parseInt(i / (values.length - 1) * 100)] = {
                        label: <span className="timeMarkMin" >{filter[0].value[0].split("/")[0]}</span>,
                    }
                } else if (i === (values.length - 1)) {
                    marks[parseInt(i / (values.length - 1) * 100)] = {
                        label: <span className="timeMarkMax" >{filter[0].value[filter[0].value.length - 1].split("/")[0]}</span>,
                    }
                }
                else {
                    marks[parseInt(i / (values.length - 1) * 100)] = {
                        label: <span></span>,
                    }
                }
            }
            return (
                <div className="TemporalFilter">
                    <div className="topicContent">
                        <Tooltip title={field}>
                            <p className="fieldText">{field}</p>
                        </Tooltip>
                    </div>

                    <div className="topicDetails">
                        <Slider range={true}
                            marks={marks}
                            defaultValue={defaultValue}
                            dots={false}
                            step={1}
                            tipFormatter={(value) => value === 100 ? values[values.length - 1] : values[parseInt(value / (100 / (values.length)))]}
                            onChange={(value) => {
                                let start = parseInt(value[0] / 100 * (values.length));
                                let end = value[1] === 100 ? values.length - 1 : parseInt(value[1] / 100 * (values.length));
                                let filter = {
                                    'field': props.info.field,
                                    'type': 'equal',
                                    'value': props.info.values.filter((d, i) => {
                                        return i >= start && i <= end
                                    }),
                                    'value2': value,
                                    'value3': [start, end]
                                }
                                let filterTitle = { 'field': props.info.field, 'type': 'temporal', 'title': props.info.values[start] + '-' + props.info.values[end] };

                                let filters = props.filters;
                                let filtersTitle = props.filtersTitle;


                                let findFilter = 'findFilter-false';
                                let findFilterTitle = 'findFilterTitle-false';
                                for (let i = 0; i < filters.length; i++) {
                                    if (filters[i].field === filter.field) {
                                        //   filters[i] = filter;
                                        findFilter = i;
                                        break;
                                    }
                                }


                                for (let i = 0; i < filtersTitle.length; i++) {
                                    if (filtersTitle[i].field === filterTitle.field) {
                                        filtersTitle[i] = filterTitle;
                                        findFilterTitle = i;
                                        break;
                                    }
                                }


                                if (findFilter === 'findFilter-false') {
                                    filters.push(filter);
                                }

                                if (findFilterTitle === 'findFilterTitle-false') {
                                    filtersTitle.push(filterTitle);
                                }


                                // - new rangeold rangechangesmooth
                                if (findFilter !== 'findFilter-false' && filters[findFilter].value3[0] === start && filters[findFilter].value3[1] === end) {
                                    setDefaultValue(value);
                                }
                                else {
                                    setDefaultValue(value);
                                    filter.value2 = [start * (100 / (values.length - 1)), end * (100 / (values.length - 1))];
                                    filters[findFilter] = filter;

                                    props.updateFilters(filters);
                                    props.updateFiltersTitle(filtersTitle);
                                    props.updateFiltersChangeFlag(true);
                                    this.props.orderFacts(0, 0);
                                }



                            }}
                        />
                    </div>
                    <Divider />
                </div>

            )

        };
        //
        const RegenerateView = () => {
            return (
                <div className="regenerate">
                    <Button className='regenerate-btn' type="primary" block onClick={() => this.filteredgenerate(config.url.generateRandomly)}>{initDone && intl.get("Regenerate")}</Button>
                </div>
            )
        };
        return (
            <div className='Topic-Filter'>
                    <div className="filter-content">
                        {
                            schema.filter(d => d.type !== 'text' && d.type !== "ID").map((info, index) => {
                                if (info.type === 'categorical' || info.type === 'geographical') {
                                    return (
                                        <CategoricalFilter key={"Categorical" + index} info={info} filters={this.props.filters} updateFilters={this.props.updateFilters} filtersTitle={this.props.filtersTitle} updateFiltersTitle={this.props.updateFiltersTitle} filteredgenerate={this.filteredgenerate} postUrl={this.getGenerateURL("")} updateFiltersChangeFlag={this.props.updateFiltersChangeFlag} />
                                    );
                                } else if (info.type === 'numerical') {
                                    return (
                                        <NumericalFilter key={"Numerical" + index} info={info} draw={draw} id={"numericalFilter-" + index} filters={this.props.filters} filterIndex={index} updateFilters={this.props.updateFilters} updateFiltersChangeFlag={this.props.updateFiltersChangeFlag} />
                                    );
                                } else if (info.type === 'temporal') {
                                    return (
                                        <TemporalFilter key={"Temporal" + index} info={info} filters={this.props.filters} updateFilters={this.props.updateFilters} filtersTitle={this.props.filtersTitle} updateFiltersTitle={this.props.updateFiltersTitle} filteredgenerate={this.filteredgenerate} postUrl={this.getGenerateURL("")} updateFiltersChangeFlag={this.props.updateFiltersChangeFlag} />
                                    );
                                }
                            })
                        }
                    </div >
                    < div className="leftTopicPanelButton" style={{ marginTop: "5px" }}>
                        <RegenerateView />
                    </div>
                </div>
        )
    }
}